.poll-container {
  width: 100%;
  height: 100vh;
  display: grid;
  text-align: center;
  background-color: #00695c;
  color: white;
}

.poll-button {
  background-color: white;
  margin: 20px 20px 0px 20px;
  padding: 20px;
  color: black;
  font-size: 18px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px #00000099;
}

.border-bottom {
  margin-bottom: 20px;
}

.poll-button:active {
  box-shadow: 0px 8px 8px #00000099;
}
