.form-container {
  margin: 0px;
  padding: 0px;
  height: 100vh;
  width: 100%;
}

.form {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.input-field {
  left: 50%;
  width: 70%;
  -webkit-transform: translate(-50%, 0%);
  transform: translate(-50%, 0%);
}

.fab {
    margin: 30px !important;
    position: absolute !important;
    bottom: 0px;
    right: 0px;
}